import React, { useEffect, useState } from "react";
import { config } from "../../config/env";
import "./ReadyStatus.scss";

export default function ReadyStatus() {
    const [status, setStatus] = useState({ ready: false, loading: true });

    useEffect(() => {
        const checkStatus = () => {
            fetch(config.endpoint)
                .then(res => {
                    setStatus({ ready: res.status === 200, loading: false });
                })
                .catch(() => {
                    setStatus({ ready: false, loading: false });
                });
        };

        checkStatus();
        const intervalId = setInterval(checkStatus, 30000); // Check every 30 seconds

        return () => clearInterval(intervalId);
    }, []);

    const getStatusClass = () => {
        if (status.loading) return "loading";
        return status.ready ? "online" : "offline";
    };

    return (
        <div className={`server-status ${getStatusClass()}`}></div>
    );
}