import { useParams } from "react-router-dom";
import { decrypt } from "crypto-js/aes";
import { enc } from "crypto-js";
import { config } from "../../config/env";
import { useState } from "react";
import NewTab from "../NewTab/NewTab";
import { saveAs } from "file-saver";

export default function Scan() {
  const { hash } = useParams();
  const [downloadedText, SetDownloadedText] = useState("");
  const [_, locker, token] = decrypt(atob(hash), "secret")
    .toString(enc.Utf8)
    .split("$$");

  const myHeaders = new Headers();
  myHeaders.append("Content-Type", "application/json");

  fetch(config.api + "supply/loot", {
    method: "POST",
    body: JSON.stringify({
      locker: locker?.split("-")[1],
      token: token,
    }),
    headers: myHeaders,
  })
    .then((response) => {
      if (!response.ok) {
        throw new Error("Network response was not ok");
      }
      const contentType = response.headers.get("content-type");
      if (contentType && contentType.includes("text/plain")) {
        return response.text();
      } else {
        return response.blob();
      }
    })
    .then((result) => {
      if (result instanceof Blob) {
        const filename = `download_${new Date().getTime()}.txt`;
        saveAs(result, filename);
      } else {
        SetDownloadedText(result);
      }
    })
    .catch((error) => {
      console.error("Error:", error);
      alert("An error occurred while processing the response.");
    });
  return (
    <div>
      {/*{downloadedText} */}
      {downloadedText && <NewTab text={downloadedText} />}
    </div>
  );
}
