import React, { useState } from "react";
import "./TabMenu.scss";
import LoadSupply from "../LoadSupply/LoadSupply";
import LootDrop from "../LootDrop/LootDrop";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faUpload, faDownload } from "@fortawesome/free-solid-svg-icons";

export default function TabMenu() {
  const [activeTab, setActiveTab] = useState("load");

  return (
    <div id="box-container">
      <div id="v-pills-tab" role="tablist" aria-orientation="vertical">
        <button
          className={`nav-link ${activeTab === "load" ? "active" : ""}`}
          onClick={() => setActiveTab("load")}
        >
          <FontAwesomeIcon icon={faUpload} className="icon" />
          <span className="text">Load Supply</span>
        </button>
        <button
          className={`nav-link ${activeTab === "loot" ? "active" : ""}`}
          onClick={() => setActiveTab("loot")}
        >
          <FontAwesomeIcon icon={faDownload} className="icon" />
          <span className="text">Loot Drop</span>
        </button>
      </div>
      <div className="tab-content">
        {activeTab === "load" && <LoadSupply />}
        {activeTab === "loot" && <LootDrop />}
      </div>
    </div>
  );
}