import React from "react";
import QRCode from "react-qr-code";
import { config } from "../../config/env";
import { encrypt } from "crypto-js/aes";

export default function Modal({
  title,
  text,
  token,
  openNewTab,
  IsQRCode,
  ModelBody,
  modalId,
}) {
  const closeModal = () => {
    var modal = document.getElementById("modal" + modalId);
    modal.classList.remove("show");
    modal.style.display = "none";
  };

  const openScanInNewTab = () => {
    const encodedData = btoa(
      encrypt("text$$" + text + "$$" + token, "secret").toString()
    );
    const url = window.location.origin + "#scan/" + encodedData;
    window.open(url, "_blank");
  };

  return (
    <>
      <div
        className="modal fade"
        id={"modal" + modalId}
        data-bs-backdrop="static"
        data-bs-keyboard="false"
        tabIndex="-1"
        aria-labelledby="staticBackdropLabel"
        aria-hidden="true"
      >
        <div className="modal-dialog modal-dialog-centered modal-dialog-scrollable">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title" id="staticBackdropLabel">
                {title}
              </h5>
              <button
                type="button"
                className="btn-close"
                data-bs-dismiss="modal"
                aria-label="Close"
                onClick={closeModal}
              ></button>
            </div>
            <div className="modal-body" id="modal-body">
              {IsQRCode && (
                <>
                  {"Content loaded in " + text}
                  <div
                    style={{
                      height: "auto",
                      margin: "0 auto",
                      maxWidth: 64,
                      width: "100%",
                    }}
                  >
                    <QRCode
                      size={256}
                      style={{
                        height: "auto",
                        maxWidth: "150%",
                        width: "150%",
                      }}
                      value={
                        config.home +
                        "#scan/" +
                        btoa(
                          encrypt(
                            "text$$" + text + "$$" + token,
                            "secret"
                          ).toString()
                        )
                      }
                      viewBox={`0 0 256 256`}
                    />
                  </div>
                </>
              )}
              {ModelBody && <div>{ModelBody}</div>}
            </div>

            <div className="modal-footer">
              {openNewTab && (
                <button
                  type="button"
                  className="btn btn-secondary"
                  onClick={openScanInNewTab}
                >
                  Open in new tab
                </button>
              )}
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
