import React, { useState } from "react";
import "./LootDrop.scss";
import { config } from "../../config/env";
import { saveAs } from "file-saver";
import Modal from "../Modal/Modal";

export default function LootDrop() {
  const [locker, SetLocker] = useState("");
  const [token, SetToken] = useState("");
  const [modalText, SetModalText] = useState("");
  const [modalTitle, SetModalTitle] = useState("");
  

  const DownloadSupply = () => {
    console.log(token);
    console.log(locker);
    if (locker === "" || token === "") {
      if (locker === "") {
        alert("Please enter the Locker number");
      }
      if (token === "") {
        alert("Please enter the valid Token number");
      }
    } else {
      const myHeaders = new Headers();
      myHeaders.append("Content-Type", "application/json");

      fetch(config.api + "supply/loot", {
        method: "POST",
        body: JSON.stringify({
          locker: locker,
          token: token,
        }),
        headers: myHeaders,
      })
        .then((response) => {
          if (!response.ok) {
            throw new Error("Network response was not ok");
          }
          const contentType = response.headers.get("content-type");
          if (contentType && contentType.includes("text/plain")) {
            return response.text();
          } else {
            return response.blob();
          }
        })
        .then((result) => {
          if (result instanceof Blob) {
            const filename = `download_${new Date().getTime()}.txt`;
            saveAs(result, filename);
          } else {
            const text = result;
            SetModalText(text);
            SetModalTitle("Supplies Loated Successfully from Locker:" + locker);
            var el = document.getElementById("modalloot");
            el.classList.add("show");
            el.style.display = "block";
          }
        })
        .catch((error) => {
          console.error("Error:", error);
          alert("An error occurred while processing the response.");
        });
    }
  };
  return (
    <div className="container loot-drop">
      <form
        className="h-100"
      >
        <div className={"row h-75"}>
          <div
            className={
              "form-group locker col-12 col-lg-6 m-auto "
            }
          >
            <label>
              Enter the locker number you recieved while loading Supply
            </label>
            <input
              type="text"
              name="locker"
              className="form-control p-3"
              placeholder="Enter the locker number"
              required
              value={locker}
              onChange={(e) => SetLocker(e.target.value)}
            />
          </div>
          <div
            className={
              "form-group token  col-12 col-lg-6 m-auto "
            }
          >
            <label>Enter the token number associated with this locker</label>
            <input
              type="text"
              name="token"
              className="form-control p-3"
              placeholder="Enter the token number"
              required
              value={token}
              onChange={(e) => SetToken(e.target.value)}
            />
          </div>
        </div>
        <div className="row m-2">
          <button
            type="button"
            className="btn btn-success"
            onClick={DownloadSupply}
          >
            Loot Text From Drop
          </button>
        </div>
      </form>
      <Modal
        title={modalTitle}
        text={modalText}
        openNewTab={true}
        IsQRCode={false}
        ModelBody={modalText}
        modalId="loot"
      />
    </div>
  );
}
